<template>
  <div>
    <el-dialog :title="info.title" :visible.sync="info.show" @opened="opened">
      <el-form :model="form">
        <!-- 联动效果 -->
        <el-form-item label="一级分类" :label-width="formLabelWidth">
          <!-- @change 是element UI库的方法：选中值发生变化时触发 -->
          <el-select v-model="form.first_cateid" @change="changeCate(false)">
            <el-option label="--请选择---" value disabled></el-option>
            <!-- 遍历一级分类 -->
            <el-option
              v-for="item in cateList"
              :key="item.id"
              :label="item.catename"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="二级分类" :label-width="formLabelWidth">
          <el-select v-model="form.second_cateid">
            <el-option label="--请选择---" value disabled></el-option>
            <!-- 遍历二级分类 -->
            <el-option
              v-for="item in secondCate"
              :key="item.id"
              :label="item.catename"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="商品名称" :label-width="formLabelWidth">
          <el-input v-model="form.goodsname"></el-input>
        </el-form-item>

        <el-form-item label="价格" :label-width="formLabelWidth">
          <el-input v-model="form.price"></el-input>
        </el-form-item>

        <el-form-item label="市场价格" :label-width="formLabelWidth">
          <el-input v-model="form.market_price"></el-input>
        </el-form-item>

        <!-- 照片上传 -->
        <el-form-item label="图片" :label-width="formLabelWidth">
          <!-- on:change ele自定义方法  -->
          <el-upload
            class="avatar-uploader"
            action="#"
            :show-file-list="false"
            :on-change="changeImg"
          >
            <img v-if="imageUrl" :src="imageUrl" class="avatar" />
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>
        <!-- 联动效果 -->
        <el-form-item label="商品规格" :label-width="formLabelWidth">
          <el-select v-model="form.specsid" @change="changeSpec(false)">
            <el-option label="--请选择---" value disabled></el-option>
            <!-- 遍历商品规格 -->
            <el-option
              v-for="item in specList"
              :key="item.id"
              :label="item.specsname"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <!-- 遍历规格属性 -->
        <el-form-item label="规格属性" :label-width="formLabelWidth">
          <el-select v-model="form.specsattr" multiple>
            <el-option label="--请选择---" value disabled></el-option>
            <el-option v-for="(item,index) in secondSpec" :key="index" :label="item" :value="item"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="是否新品" :label-width="formLabelWidth">
          <el-radio v-model="form.isnew" :label="1">是</el-radio>
          <el-radio v-model="form.isnew" :label="2">否</el-radio>
        </el-form-item>
        <el-form-item label="是否热卖" :label-width="formLabelWidth">
          <el-radio v-model="form.ishot" :label="1">是</el-radio>
          <el-radio v-model="form.ishot" :label="2">否</el-radio>
        </el-form-item>
        <el-form-item label="描述" :label-width="formLabelWidth">
          <div id="div1"></div>
        </el-form-item>
        <!-- 状态 -->
        <el-form-item label="状态" :label-width="formLabelWidth">
          <el-switch
            v-model="form.status"
            active-color="skyblue"
            inactive-color="#ff4949"
            :active-value="1"
            :inactive-value="2"
          ></el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" @click="confirm" v-if="info.isAdd">确 定</el-button>
        <el-button type="primary" @click="update" v-else>修改</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import E from "wangeditor";
import { addGoods, getOneGoods, updateGoods } from "../../..//utils/req";
import { successAlert } from "../../..//utils/alerts";
import { mapActions, mapGetters } from "vuex";
export default {
  props: ["info"],
  data() {
    return {
      imageUrl: "",
      formLabelWidth: "120px",
      secondCate: [], // 定义二级商品分类
      secondSpec: [], // 定义二级规格属性
      editor: "", //富文本框
      form: {
        first_cateid: "",
        second_cateid: "",
        goodsname: "",
        price: "",
        market_price: "",
        img: "",
        specsid: "",
        specsattr: [],
        isnew: 1,
        ishot: 1,
        status: 1,
        description: "",
      },
    };
  },
  computed: {
    ...mapGetters({
      cateList: "cate/cateList",
      specList: "spec/specList",
    }),
  },
  methods: {
    ...mapActions({
      reqCateList: "cate/cateListActions",
      reqSpecList: "spec/specListActions",
      reqGoodsList: "goods/goodsListActions",
      reqGoodsCount: "goods/countActions",
    }),
    // 自定义方法  修改文件
    changeImg(e) {
      //处理上传文件的大小
      if (e.size > 2 * 1024 * 1024) {
        warningAlert("文件大小不能超过2M");
        return;
      }
      // 处理文件后缀名
      let suffix = [".jpg", ".png", ".gif", ".jpeg"];
      //  从后边算起 '.' 开始分割
      let sufNmae = e.name.slice(e.name.lastIndexOf("."));
      // 进行格式判断  .some为遍历数组  即有一个为真即为真
      if (!suffix.some((item) => item == sufNmae)) {
        warningAlert("上传文件格式不正确");
        return;
      }

      // 上传显示的文件  将文件生成url地址并显示
      this.imageUrl = URL.createObjectURL(e.raw);
      // 将文件存入 form.img
      this.form.img = e.raw;
    },
    // 改变一级分类时 获取二级分类  传递一个参数 为布尔值
    changeCate(boo) {
      // 进行判断  如果为真 则二级分类id为初始值（空）
      if (!boo) {
        this.form.second_cateid = "";
      }
      // 点击一级分类的时候获取下标 ，根据下标找到一级分类下的children
      let index = this.cateList.findIndex(
        (item) => item.id == this.form.first_cateid
      );
      this.secondCate = this.cateList[index].children;
    },

    // 改变规格时  获取商品规格属性 传递一个参数 为布尔值
    changeSpec(boo) {
      // 进行判断  如果为真 则二级分类id为初始值（空）
      if (!boo) {
        this.form.specsattr = "";
      }
      // 点击一级分类的时候获取下标 ，根据下标找到一级分类下的children
      let index = this.specList.findIndex(
        (item) => item.id == this.form.specsid
      );
      this.secondSpec = this.specList[index].attrs;
    },
    // 加载完成还没有调用e-dialog  ,所以调用ele的方法 opened 进行回调
    opened() {
      this.editor = new E("#div1");
      this.editor.create(); //调用方法
      this.editor.txt.html(this.form.description); // 让其回调的时候内容清空
    },
    // 点击取消时
    cancel() {
      this.info.show = false;
        this.form = {
          first_cateid: "",
          second_cateid: "",
          goodsname: "",
          price: "",
          market_price: "",
          img: "",
          specsid: "",
          specsattr: [],
          isnew: "1",
          ishot: "1",
          status: 1,
          description: "",
        };
        this.imageUrl = "";
    },
    // 点击确定的时候
    confirm() {
      // 需要将富文本框的内容转换
      this.form.description = this.editor.txt.html();
      addGoods(this.form).then((res) => {
        successAlert(res.data.msg);
        this.cancel();
        this.reqGoodsList();
        this.reqGoodsCount();
      });
    },
    // 点击编辑时  获取商品详情
    getDetail(id) {
      getOneGoods({id}).then((res) => {
        this.form = res.data.list;
        this.form.id = id;
        this.imageUrl = this.$preImg + this.form.img;
        this.changeCate(true);//获取二级分类
        this.changeSpec(true);// 获取规格属性
        this.form.specsattr=this.form.specsattr.split(',');// 将数组切割
      });
    },
    // 修改
    update(){
      // 需要将富文本框的内容转换
      this.form.description = this.editor.txt.html();
      updateGoods(this.form).then(res=>{
        successAlert(res.data.msg);
        this.cancel();
        this.reqGoodsList();
      })

    }
  },
  mounted() {
    this.reqCateList();
    this.reqSpecList();
    const editor = new E("#div1");
    editor.create();
  },
};
</script>

<style scoped>
.avatar-uploader >>> .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader >>> .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>


