<template>
  <div>
    <el-table
      :data="tableData"
      style="width: 100%;margin-bottom: 20px;"
      row-key="id"
      border
      :tree-props="{children: 'children'}"
    >
      <el-table-column prop="id" label="商品编号"></el-table-column>
      <el-table-column prop="goodsname" label="商品名称"></el-table-column>
      <el-table-column prop="price" label="商品价格">
        <template v-slot="p">{{p.row.price |priceFilter}}</template>
      </el-table-column>
      <el-table-column prop="market_price" label="市场价格">
        <template v-slot="p">{{p.row.price |priceFilter}}</template>
      </el-table-column>
      <el-table-column label="图片">
        <!-- 使用插槽作用域 -->
        <template v-slot="prop">
          <img :src="$preImg+prop.row.img" alt v-if="prop.row.img" />
        </template>
      </el-table-column>
      <el-table-column prop="isnew" label="是否新品">
        <template v-slot="prop">
          <el-button type="primary" v-if="prop.row.isnew==1">是</el-button>
          <el-button type="danger" v-else>否</el-button>
        </template>
      </el-table-column>
      <el-table-column prop="ishot" label="是否热卖">
        <template v-slot="prop">
          <el-button type="primary" v-if="prop.row.ishot==1">是</el-button>
          <el-button type="danger" v-else>否</el-button>
        </template>
      </el-table-column>
      <el-table-column prop="status" label="状态">
        <template v-slot="prop">
          <el-button type="primary" v-if="prop.row.status==1">启用</el-button>
          <el-button type="danger" v-else>禁止</el-button>
        </template>
      </el-table-column>
      <el-table-column prop="status" label="操作">
        <template v-slot="prop">
          <el-button type="primary" @click="edit(prop.row.id)">编辑</el-button>
          <el-button type="danger" @click="del(prop.row.id)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="prev, pager, next"
      :page-size="size"
      :total="count"
      @current-change="Page"
    ></el-pagination>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import { delGoods } from "../../../utils/req";
import { successAlert } from "../../../utils/alerts";
export default {
  data() {
    return {};
  },
  computed: {
    ...mapGetters({
      tableData: "goods/goodsList",
      count: "goods/count",
      size: "goods/size",
    }),
  },
  methods: {
    ...mapActions({
      reqGoodsList: "goods/goodsListActions",
      reqCount: "goods/countActions",
      reqPage: "goods/pageActions",
    }),
    // 当前页数
    Page(page) {
      // 请求页码
      this.reqPage(page),
        // 请求管理员列表
        this.reqGoodsList();
    },
    // 编辑
    edit(id) {
      this.$emit("edit", id);
    },
    del(id) {
      // 弹出是否要删除的对话框
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          delGoods({ id }).then((res) => {
            //  调用列表请求方法  达到删除时同时刷新
            successAlert(res.data.msg);
            this.reqGoodsList();
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
  },
  mounted() {
    this.reqGoodsList();
    this.reqCount();
  },
};
</script>

<style scoped>
img {
  width: 100px;
  height: 100px;
}
</style>


